.main-menu__general {
	margin-left: auto;
	margin-right: 30px;
}

.menu__list {
	display: flex;
}

.menu__item {
	margin-left: 30px;
	position: relative;
}

.menu__item:hover .drop-menu {
	top: 100%;
	opacity: 1;
	margin-top: 0;
	visibility: visible;
}

.menu__link {
	display: block;
	font-size: 16px;
	line-height: 68px;
	color: #000;
	text-decoration: none;
	padding-right: 15px;
	background: url("../img/menuArrowDown.png") 100% 50% no-repeat;
	transition: .5s ease;
}

.menu__link:hover {
	color: gold;
}

.drop-menu {
	position: absolute;
	top: -9999px;
	top: 100%;
	left: -20px;
	margin-top: 30px;
	opacity: 0;
	visibility: hidden;
	transition: opacity .3s ease, margin-top .5s ease;
	z-index: 100;
	background-color: #fff;
	width: 150px;
	padding: 15px;
	border: 1px solid #ccc;
	box-shadow: 0 0 5px 0 #ccc;
	border-radius: 3px;
}

.drop-menu ul li {
	margin-bottom: 10px;
}

.drop-menu ul li:last-child {
	margin-bottom: 0;
}

.drop-menu ul li a {
	display: block;
	width: 100%;
	text-decoration: none;
	color: #000;
	transition: .5s ease;
}

.drop-menu ul li a:hover {
	color: gold;
}

.drop-menu:before {
	position: absolute;
	content: '';
	display: block;
	left: 20px;
	bottom: 101%;
	width: 0px;
	height: 0px;
	border-style: solid;
	border-width: 0 5px 5px 5px;
	border-color: transparent transparent #ccc transparent;
}

