}

.product__price {
	font-weight: 22px;
	color: #373737;
	margin-bottom: 12px;
}

.slider__btn {
	height: 34px;
	line-height: 38px;
	max-width: 120px;
	color: #fff;
	cursor: pointer;
	padding: 0 10px;
	font-size: 12px;
	margin-bottom: 12px;
	position: static;
}

.slider__btn::after {
	display: none;
}

.main-slider {
	padding-top: 50px;
}

.nav {
	display: flex;
	align-items: center;
	justify-content: flex-start;
}

.nav__filter {
	padding: 20px;
	font-size: 15px;
	color: #555;
	cursor: pointer;
	text-transform: uppercase;
	transition: .5s ease;
}

.nav__filter:hover {
	background: rgba(253, 146, 109, 0.5);
}

.nav__filter.active {
	background-color: #fd926d;
	color: #fff;
}

.slider {
	height: 456px;
	background-color: #fd926d;
}

.slider__inner {
	opacity: 0;
	visibility: hidden;
	display: none;
}

.slick-center {
	transition: 1.5s ease;
	transform: scale(1.04);
}

.slick-center .slider__item {
	box-shadow: 0 5px 18px rgba(0, 0, 0, 0.35);
	position: relative;
}

.slick-center .slider__item .slider__inner {
	position: absolute;
	left: 50%;
	top: 20%;
	transform: translateX(-50%);
	display: block;
	width: 130px;
	height: 130px;
	background-color: #3d3d3d;
	border-radius: 50%;
	text-transform: uppercase;
	color: #fff;
	text-decoration: none;
	line-height: 158px;
	background-image: url(../img/inner.png);
	background-repeat: no-repeat;
	background-position: 50% 35%;
	transition: opacity .7s ease;
	opacity: 0;
	visibility: hidden;
}

.slick-center .slider__item:hover .slider__inner {
	opacity: 1;
	visibility: visible;
	display: block;
}

.slider__wrapper {
	width: calc(100% + 30px);
	padding: 15px;
	margin-left: -15px;
	overflow: hidden;
}

.main-slider > .container {
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
}

.slick-arrows {
	display: flex;
}

.arrow-lt,
.arrow-gt {
	width: 45px;
	height: 45px;
	background: rgba(253, 146, 109, 0.5);
	transition: .5s ease;
	cursor: pointer;
	font-size: 30px;
	line-height: 47px;
	text-align: center;
	font-weight: 400;
	color: #999;
}

.arrow-lt:hover,
.arrow-gt:hover {
	background: #fd926d;
	color: #fff;
}

.clearence {
	padding: 20px 0;
	text-align: center;
}

.clearence > .container {
	overflow: hidden;
}

.clearence__img {
	display: inline-block;
	text-align: center;
}

.clearence__link {
	display: inline-block;
	position: relative;
	text-align: center;
}

.clearence__link:before, .clearence__link:after {
	position: absolute;
	top: 50%;
	width: 1000px;
	height: 1px;
	background-color: #d8d8d8;
	content: '';
}

.clearence__link:after {
	left: 100%;
	margin-left: 20px;
}

.clearence__link:before {
	right: 100%;
	margin-right: 20px;
}

.promo {
	margin-bottom: 60px;
}

.promo > .container {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
}

.brands {
	padding: 20px 0;
	position: relative;
	border: 1px solid #d8d8d8;
}

.brands__title {
	position: absolute;
	display: block;
	padding: 0 20px;
	top: -8px;
	z-index: 10;
	left: 50%;
	transform: translateX(-50%);
	text-transform: uppercase;
	color: #414141;
	font-size: 15px;
	font-weight: 700;
	background-color: #fff;
}

.bradns-wrapper {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.about {
	text-align: center;
	padding: 30px 0;
}

.about__title {
	font-weight: 400;
	font-size: 24px;
	color: #414141;
	margin-bottom: 15px;
}

.about__text {
	font-size: 14px;
	line-height: 1.3;
	color: #999;
}

.payment {
	padding: 20px 0;
	background-color: #f7f7f7;
}

.payment > .container {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
}

.payment > .container a {
	margin-right: 15px;
}

.payment__text {
	color: #ababab;
	font-size: 12px;
	margin-right: 15px;
}

.links-blocks {
	padding: 32px 0;
	background-color: #ededed;
}

.links-blocks > .container {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}

.links-block_100 {
	margin-top: 20px;
}

.links-block__title {
	color: #544f4d;
	font-size: 16px;
	line-height: 1.2;
	text-transform: capitalize;
	margin-bottom: 15px;
}

.links-block__item {
	margin-bottom: 10px;
}

.links-block__item:last-child {
	margin-bottom: 0;
}

.links-block__link {
	font-size: 12px;
	color: #544f4d;
	text-decoration: none;
	transition: color .2s ease;
	cursor: pointer;
}

.links-block__link:hover {
	color: #000;
}

.social {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	max-width: 140px;
	flex-flow: row wrap;
}

.social__item {
	display: block;
	width: 35px;
	height: 35px;
	line-height: 44px;
	background-color: #4c4c4b;
	border-radius: 50%;
	text-align: center;
	transition: .5s ease;
	margin-right: 10px;
	margin-bottom: 10px;
}

.social__item svg {
	font-size: 20px;
	fill: #fff;
}

.social__item:hover {
	background-color: #fff;
}

.social__item:hover svg {
	fill: #4c4c4b;
}

.footer {
	padding: 24px 0;
	background-color: #000;
	color: #8d8d8d;
	font-size: 11px;
	font-weight: 400;
}

.footer > .container {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.footer-menu__item {
	display: inline-block;
	position: absolute;
	padding-right: 22px;
	position: relative;
}

.footer-menu__item:last-child {
	margin-right: 10px;
}

.footer-menu__item:last-child::after {
	content: '';
}

.footer-menu__item::after {
	position: absolute;
	top: 0;
	right: 10px;
	content: '|';
}

.footer-menu__link {
	color: #8d8d8d;
	text-decoration: none;
	transition: .5s ease;
}

.footer-menu__link:hover {
	color: #fff;
}

.copyright {
	text-align: right;
	line-height: 1.6;
}
